import { createStore,combineReducers } from "redux";//combineReducersreducer
import {composeWithDevTools} from 'redux-devtools-extension'; //redux
import {sellerCart,site,log,collect} from './cart/reducer'; //cart reducer
const shoppingCart = {
  log,
  site,
  collect,
  sellerCart,
}
const rootReducer = combineReducers(shoppingCart);

let store = createStore(rootReducer,composeWithDevTools()); //

export default store;


