import Mock from 'mockjs';


//  mock.Random 
const Random = Mock.Random;
// mock
const produceNewsData = function() {
    let articles = [];
    for (let i = 0; i < 20; i++) {
        let newArticleObject = {
            img: Random.dataImage('300x250', 'mock'), // Random.dataImage( size, text )  Base64 
            name: Random.cname(), // Random.cname() 
            score:Random.natural(0, 5),
            sellCount:Random.natural(0, 2000),
            minPrice:Random.natural(15, 40),
            deliveryPrice:Random.natural(0,10),
            deliveryTime:Random.natural(0,60),
            
        }
        articles.push(newArticleObject)
    }
 
    return {
        articles: articles
    }
}

// Mock.mock( url, post/get , )
Mock.mock(/http:/, 'get', produceNewsData);



