import Server from './server';
import '../mockJs/mock';

class AXIOS extends Server {
  /** 
  *  
  *  @url http:
  *  success:{code:0}
  *  @method get
  *  @return {promise}
  */
  
  async userlLogin (params={}) {                                        // async/await 
    try {                                                             //
      let result = await this.axios('post','/user/signup/',params);        //
      if (result && result.code === 0){                               // 
        return result;
      } else {                                                       
        let err = {
          tip: '',
          Response: result,
          data: params,
          url: 'http',
        }
        throw err;                                                     // 
      }
    } catch (err) {                                                    // trycatch
      throw err;                                                       // 
    }
  }
  /**
  *  
  *  @url http:
  *  success:{code:0}
    */
  async verifyImg (params={}) {
    try {
      let result = await this.axios('post','/user/imagecode/');
      if (result && result.code === 0) {
        return result;
      } else {
        let err = {
          tip: 'T',
          Response: result,
          data: params,
          url: 'http',
        }
        throw err;   
      }
    }  catch (err) {                                                    // trycatch
      throw err;                                                       // 
    }
  }

  async getSite (params={}) {
    try {
      let result = await this.axios('get','http://restapi.amap.com/v3/ip?key=4b5a60a2b9f51d7de51b938dbd3d5866');
      if (result) {
        return result;
      } 
      else {
        let err = {
          tip: '',
          Response: result,
          data: params,
          url: 'http',
        }
        throw err;
      }
    } 
    catch (err){
      throw(err);
    }
  }
  /**
   * 
  */
  async getTest (params={}) {
    try {
      let result = await this.axios('get',' http://106.12.22.19');
      if (result) {
        return result;
      } 
      else {
        let err = {
          tip: '',
          Response: result,
          data: params,
          url: 'http',
        }
        throw err;
      }
    } 
    catch (err){
      throw(err);
    }
  }

  async getSellerMesaage (params={}) {
    try {
      let result = await this.axios('get',' http://106.12.22.19');
      if (result) {
        return result;
      } 
      else {
        let err = {
          tip: '',
          Response: result,
          data: params,
          url: 'http',
        }
        throw err;
      }
    } 
    catch (err){
      throw(err);
    }
  }
}





export default new AXIOS();


