import React,{Component} from 'react'
import {NavLink,Route,Switch} from 'react-router-dom';
import SellerHead from '../sellerHead/sellerHead.js';
import SellerGood from  '../sellerGood/sellerGood.js';
import SellerRating from  '../sellerRating/sellerRating.js';
import SellerInfo from  '../sellerInfo/sellerInfo.js';
// import '../../common/animated/animated.less';
// import { TransitionGroup, CSSTransition } from "react-transition-group";
import './shop.less';

class Shop extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      showValidationMessage: false,
      selectFoods:[0,1,2,3,4],
      seller: {
        "name": "",
        "description": "",
        "deliveryTime": 38,
        "score": 4.2,
        "serviceScore": 4.1,
        "foodScore": 4.3,
        "rankRate": 69.2,
        "minPrice": 20,
        "deliveryPrice": 4,
        "ratingCount": 24,
        "sellCount": 90,
        "bulletin": "020082013",
        "supports": [
          {
            "type": 0,
            "description": "285"
          },
          {
            "type": 1,
            "description": "VC8"
          },
          {
            "type": 2,
            "description": ""
          },
          {
            "type": 3,
            "description": ","
          },
          {
            "type": 4,
            "description": ","
          }
        ],
        "avatar": "http://static.galileo.xiaojukeji.com/static/tms/seller_avatar_256px.jpg",
        "pics": [
          "http://fuss10.elemecdn.com/8/71/c5cf5715740998d5040dda6e66abfjpeg.jpeg?imageView2/1/w/180/h/180",
          "http://fuss10.elemecdn.com/b/6c/75bd250e5ba69868f3b1178afbda3jpeg.jpeg?imageView2/1/w/180/h/180",
          "http://fuss10.elemecdn.com/f/96/3d608c5811bc2d902fc9ab9a5baa7jpeg.jpeg?imageView2/1/w/180/h/180",
          "http://fuss10.elemecdn.com/6/ad/779f8620ff49f701cd4c58f6448b6jpeg.jpeg?imageView2/1/w/180/h/180"
        ],
        "infos": [
          ",",
          ":,",
          "B1021340",
          ":10:00-20:30"
        ]
      },
    }
  }

  render() {
    return (
      <div className="good_warper">
        <SellerHead  privilege={this.state.seller} />
        <div className="tab">
          <NavLink exact  to= "/shop" className="tab-item"></NavLink>
          <NavLink  to= "/shop/rating" className="tab-item"></NavLink>
          <NavLink  to= "/shop/seller" className="tab-item"></NavLink>
        </div>
        <div className="sellerMain">
          <Switch>
            <Route exact  path='/shop' component={SellerGood} />
            <Route path='/shop/rating' component={SellerRating} />
            <Route path="/shop/seller" component={SellerInfo} />
          </Switch>
        </div>
      </div>
    );
  }
}
 
export default Shop;


