import axios from 'axios';                    // API

let baseURL;                                // 
// let imgUrl = '//elm.cangdu.org/img/';       // 
if(process.env.NODE_ENV === 'development'){   // 
  baseURL = 'http://106.12.22.19';               // baseURL
}else{
  baseURL = '//api.cangdu.org';               // baseURL
}
export default class Server {                     //  Server
  axios(method,url,params) {                      // axios
    return new Promise ((resolve, reject) => {    // promise resolvereject:
      if (typeof params !== 'object') params = {};// paramsparams
      let AxiosConfig = params;                   // axios
      AxiosConfig = {
        method,                   /* paramsmethod url 2params */
        url,
        baseURL:baseURL,
        timeout:30000,                            //   30000
        params:null,                              // params {object} getkey
        data:null,                                // data {object} postputpatchkey
        headers:null,                             // {string}
        withCredentials:true,                     // cookie
        validateStatus:(status)=>{         // `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
        return status >= 200 && status < 300;
        },
        ...params,                                //
      }
      axios.request(AxiosConfig).then(res => {    // 
        resolve(typeof res.data === 'object' ? res.data : JSON.parse(res.data)) // promise resolve
      },error =>{                                                               
        if (error.response) {
          reject(error.response.data,'')                                          // promise reject, 
        } else {
          reject(error,'')                                                       // promise reject, API 
        }
      })
    })
  }
}



